* {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

body {
  background: rgb(2, 0, 36);
  background: linear-gradient(
    90deg,
    rgba(2, 0, 36, 1) 0%,
    rgba(29, 29, 208, 1) 56%,
    rgba(28, 32, 209, 1) 57%,
    rgba(0, 134, 255, 0.773546918767507) 100%
  );
  color: #fff;
  padding: 15px;
}

input {
  color: black;
  font-size: 1.3rem;
  width: 100%;
}

.card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  grid-auto-rows: 200px;
  grid-gap: 10px;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

img:first-child {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
}

img:nth-child(6) {
  grid-column: 2 / 4;
  grid-row: 3 / 5;
}

img:nth-child(9) {
  grid-row: span 3;
}
